.blob-button {
	position: relative;
	font-size: 1rem;
	background-image: url('../img/hero.insm/play-blob.svg');
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;
}

.blob-button__play {
	position: absolute;
	top: 3em;
	left: 1.5em;
	display: block;
	background-image: url('../img/common.insm/play.svg');
	background-size: 3em;
	background-position: center;
	background-repeat: no-repeat;
	--size: 8em;
	--width: var(--size);
	--height: var(--size);
	width: var(--width);
	min-width: var(--width);
	max-width: var(--width);
	height: var(--height);
	min-height: var(--height);
	max-height: var(--height);
	border: 0.4em solid #fff;
	border-radius: 999em;

	@media (max-width: $tablet) {
	    --size: 3.8em;
	    background-size: 2em;
	    border-width: 0.3em;
	    top: 1.3em;
	    left: 0.4em;
	}
}

.blob-button__text {
	position: absolute;
	right: 137%;
	font-family: var(--ff-title);
	color: var(--c-primary);
	line-height: 1;
	top: 1.9em;
	display: block;
	width: 6em;
	height: 2.9em;
	background-image: url('../img/hero.insm/button-title.svg');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	z-index: 3;

	@media (max-width: $tablet) {
	    width: 4em;
	    height: 1.9em;
        top: 0.7em;
        right: 135%;
	}
}